/* login.css */

/* Fondo general de la página: textura + color */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', Arial, sans-serif;
  background-color: #f0f0f0;
  background: url('../assets/bg-texture.png') no-repeat center center fixed;
  background-size: cover;
}

/* Contenedor principal del Login */
.login-container {
  width: 90%;
  max-width: 400px;
  margin: 40px auto;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Sección del logo */
.logo-section {
  text-align: center;
  margin-bottom: 20px;
}

.janus-logo {
  max-width: 120px;
  height: auto;
  display: block;
  margin: 0 auto;
}

/* Formulario */
.login-form {
  width: 100%;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: none; 
  border: 1px solid #ccc; 
}

/* Mensajes de error */
.error {
  background-color: #ffe0e0;
  color: #c0392b;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  text-align: center;
}

/* Inputs con iconos */
.input-wrapper {
  margin-bottom: 15px;
}

.input-icon-container {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 4px;
  padding: 0 10px;
  border: 1px solid #ccc;
}

.input-icon-container input {
  flex: 1;
  border: none;
  padding: 10px;
  font-size: 0.95rem;
  background: transparent;
  outline: none;
}

.icon {
  color: #aaa;
  margin-right: 8px;
}

/* Opciones (Remember me, Forgot Password?) */
.options-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.remember-me {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  color: #555;
}

.remember-me input {
  margin-right: 5px;
}

.forgot-password {
  font-size: 0.9rem;
  color: #7c63f6;
  text-decoration: none;
}

.forgot-password:hover {
  text-decoration: underline;
}

/* Botón de Login */
.login-btn {
  width: 100%;
  background-color: #7c63f6;
  color: #fff;
  padding: 12px;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  margin-bottom: 10px;
  transition: background-color 0.3s ease;
}

.login-btn:hover {
  background-color: #6b55d7;
}

/* Enlace a registro */
.register-link {
  margin-top: 10px;
  text-align: center;
  font-size: 0.9rem;
  color: #333;
}

.register-link a {
  color: #7c63f6;
  text-decoration: none;
}

.register-link a:hover {
  text-decoration: underline;
}

/* Media Query para pantallas más grandes */
@media (min-width: 768px) {
  .login-container {
    margin-top: 80px;
  }
}
