.routines-container {
  padding: 20px;
  font-family: 'Poppins', sans-serif;
  background-color: #f9f9f9;
  min-height: 100vh;
}

.routines-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 20px;
}

.routines-header a {
  color: #7c63f6;
  text-decoration: none;
  font-weight: 600;
}

.info-icon {
  margin-left: 8px;
  color: #888;
  cursor: pointer;
}

.info-icon:hover {
  color: #444;
}

.routines-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #333;
}

.filter-section {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 20px;
  align-items: flex-end;
}

.filter-section label {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
  color: #555;
}

.filter-section input,
.filter-section select {
  margin-top: 4px;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.9rem;
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px,1fr));
  gap: 1rem;
  margin-bottom: 30px;
}

.metric-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  text-align: center;
  font-size: 1.1rem;
  color: #333;
}

.charts-grid {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 2rem;
}

.chart-block {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.chart-block.full-width {
  grid-column: 1 / -1;
}

.chart-block h3 {
  margin: 0 0 10px 0;
  font-size: 1.2rem;
  color: #444;
  text-align: center;
}

/* Icono de lápiz en cada tarjeta */
.routine-templates-page .tpl-card {
  position: relative;
}
.routine-templates-page .icon-edit {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  color: var(--primary-color);
  cursor: pointer;
  font-size: 1.1rem;
  transition: color 0.2s;
}
.routine-templates-page .icon-edit:hover {
  color: #135ba1;
}

